<template>
  <div class="left-let-page">
    <div class="search-box">
      <el-input
        v-model="searchText"
        clearable
        prefix-icon="el-icon-search"
        @input="onInput"
        @clear="onClear"
      />
    </div>
    <div v-if="restaurants.length > 0" class="search-res">
      <div v-for="item in restaurants" :key="item.uid">
        <div @click="choosePlace(item)" class="search-res-item">
          {{ item.name }}
        </div>
      </div>
    </div>
    <div id="map"></div>
  </div>
</template>
<script>
import "@/utils/leafletMap/tileLayer.baidu.js";
import { debounceFun } from "@/utils/debounceModule";

export default {
  data() {
    return {
      map: null,
      searchText: null,
      timeout: null,
      restaurants: [],
      marker: null,
      mapIcon: require("@/assets/img/mapIcon/marker.png"),
    };
  },

  methods: {
    /**
     * @Event 
     * @description: 
     * */
    initMap() {
      this.map = L.map("map", {
        minZoom: 3,
        maxZoom: 18,
        // center: [39.56, 116.20],
        center: [30.56, 120.2],
        zoom: 8,
        zoomControl: true, // 
        attributionControl: false, // leaflet map logo
        crs: L.CRS.Baidu,
      });
      L.tileLayer.baidu({ layer: "img2" }).addTo(this.map);
      this.map.on("click", this.setMapClick);
    },

    /**
     * @Event 
     * @description: 
     * */
    setMapClick(e) {
      var location = {
        name: "",
        location: e.latlng,
      };
      // this.setMarker(location)
      console.log(e, "e");
    },

    /**
     * @Event 
     * @description:  
     * */
    onInput: debounceFun(function () {
      let ak = "bOF9gGWXz76q065otbYtWN4A";
      // let url = 'https://api.map.baidu.com/place/v2/suggestion?query=%E6%9D%AD%E5%B7%9E&region=%E4%B8%AD%E5%9B%BD&city_limit=false&output=json&ak=lsV6XoYeHVAK0ditM4yBlatCbcIhGD6t'
      let url = `/place/v2/suggestion?query=${this.searchText}&region=&city_limit=false&output=json&ak=${ak}`;
      this.$axios.get(url).then((res) => {
        this.restaurants = res.data.result;
      });
    }, 800),

    onClear() {
      this.restaurants = [];
    },

    /**
     * @Event 
     * @description: 
     * */
    choosePlace(item) {
      this.restaurants = [];
      this.setMarker(item);
    },

    setMarker(item) {
      if (this.marker) {
        this.map.removeLayer(this.marker);
      }
      var markerIcon;
      if (item.name) {
        markerIcon = L.divIcon({
          className: "markerBox",
          html:
            `<div class="mapIcon"><img src="${this.mapIcon}" /><span class="markerTips">` +
            item.name +
            "</span></div>",
          iconSize: [32, 32], // marker
          iconAnchor: [28.5, 55], // 
        });
      } else {
        markerIcon = L.divIcon({
          className: "markerBox",
          html: `<div class="mapIcon"><img rc="${this.mapIcon}" /><span class="markerTips"></span></div>`,
          iconSize: [32, 32], // marker
          iconAnchor: [28.5, 55], // 
        });
      }

      const center = L.latLng(item.location.lat, item.location.lng);
      this.map.setView(center);
      this.marker = new L.Marker([item.location.lat, item.location.lng], {
        icon: markerIcon,
      });
      this.map.addLayer(this.marker);
    },
  },

  mounted() {
    this.initMap();
  },
};
</script>
