require("proj4");
require("proj4leaflet");

// const titleD = 'http://127.0.0.1:8886/{z}/{x}/{y}.png'
// const titleD = 'https://api.map.baidu.com/getscript?v=3.0&ak=7pQwLpGQZr0SdQ3vlDp8gu7ExEqjKRG1'
const bdImgOnline =
  "http://online{s}.map.bdimg.com/onlinelabel/?qt=tile&x={x}&y={y}&z={z}&styles=";
// const bdImgOnline = 'http://online{s}.map.bdimg.com/onlinelabel/?qt=tile&x={x}&y={y}&z={z}&styles=' + (option.bigfont ? 'ph' : 'pl') + '&scaler=1&p=1'
const timeOnline =
  "http://its.map.baidu.com:8002/traffic/TrafficTileService?x={x}&y={y}&level={z}&time=" +
  new Date().getTime() +
  "&label=web2D&v=017";
const mapUrl =
  "http://webrd01.is.autonavi.com/appmaptile?lang=zh_cn&size=1&scale=1&style=7&x={x}&y={y}&z={z}";
const localMapUrl =
  "http://127.0.0.1:8888/api/getLeafletMap?X={x}&Y={y}&Zoom={z}&Type=788865972";

L.CRS.Baidu = new L.Proj.CRS(
  "EPSG:900913",
  "+proj=merc +a=6378206 +b=6356584.314245179 +lat_ts=0.0 +lon_0=0.0 +x_0=0 +y_0=0 +k=1.0 +units=m +nadgrids=@null +wktext  +no_defs",
  {
    resolutions: (function () {
      level = 19;
      var res = [];
      res[0] = Math.pow(2, 18);
      for (var i = 1; i < level; i++) {
        res[i] = Math.pow(2, 18 - i);
      }
      return res;
    })(),
    origin: [0, 0],
    bounds: L.bounds([20037508.342789244, 0], [0, 20037508.342789244]),
  }
);

L.tileLayer.baidu = function (option) {
  option = option || {};
  var layer;
  var subdomains = "0123456789";
  switch (option.layer) {
    // 
    case "vec":
    default:
      layer = L.tileLayer(bdImgOnline, {
        name: option.name,
        subdomains: subdomains,
        tms: true,
      });
      break;

    //
    case "img_d":
      layer = L.tileLayer(
        "http://shangetu{s}.map.bdimg.com/it/u=x={x};y={y};z={z};v=009;type=sate&fm=46",
        {
          name: option.name,
          subdomains: subdomains,
          tms: true,
        }
      );
      break;

    //  
    case "img_z":
      layer = L.tileLayer(
        "http://online{s}.map.bdimg.com/tile/?qt=tile&x={x}&y={y}&z={z}&styles=" +
          (option.bigfont ? "sh" : "sl") +
          "&v=020",
        {
          name: option.name,
          subdomains: subdomains,
          tms: true,
        }
      );
      break;

    //custom 
    case "custom":
      //dark,midnight,grayscale,hardedge,light,redalert,googlelite,grassgreen,pink,darkgreen,bluish
      option.customid = option.customid || "midnight";
      layer = L.tileLayer(
        "http://api{s}.map.bdimg.com/customimage/tile?&x={x}&y={y}&z={z}&scale=1&customid=" +
          option.customid,
        {
          name: option.name,
          subdomains: "012",
          tms: true,
        }
      );
      // layer = L.tileLayer(bdImgOnline, {
      //     name: option.name, subdomains: subdomains, tms: true
      // });
      break;

    //
    case "time":
      layer = L.tileLayer(timeOnline, {
        name: option.name,
        subdomains: subdomains,
        tms: true,
      });
      break;

    //  +    demo
    case "img":
      layer = L.layerGroup([
        L.tileLayer.baidu({
          name: "",
          layer: "img_d",
          bigfont: option.bigfont,
        }),
        L.tileLayer.baidu({
          name: "",
          layer: "img_z",
          bigfont: option.bigfont,
        }),
      ]);
      break;

    //  +    demo
    case "img2":
      layer = L.layerGroup([
        L.tileLayer.baidu({
          name: "",
          layer: "vec",
          bigfont: option.bigfont,
        }),
        L.tileLayer.baidu({
          name: "",
          layer: "time",
          bigfont: option.bigfont,
        }),
      ]);
      break;

    // 
    case "localMap":
      layer = L.tileLayer(localMapUrl, {
        name: option.name,
        subdomains: subdomains,
        tms: true,
      });
      break;
  }
  return layer;
};
